* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  display: block;
  text-decoration: none;

  @include hover(transform scale(1.1));
}

ul {
  padding: 0;
  list-style: none;
}

img {
  @include hover(transform scale(1.1));
}

h1 {
  @extend %h1;
}

h2 {
  @extend %h2;
}
